import sunuiUpimg from '@/components/sunui-upimg/sunui-upimg.vue'
import {uniRate} from "@dcloudio/uni-ui"
export default {
	components: {sunuiUpimg, uniRate},
	data() {
		return {
			repairItemPhoto: '',
			star: 1,
			scoreType: '非常不好',
			comment: '',
			shopId: '',
			orderId: '',
			scoreTypeList: ['非常不好','不好','一般','好','非常好']
		}
	},
	onLoad(options) {
		this.orderId = options.orderId
		this.shopId = options.shopId
	},
	methods: {
		uploadImage(e) {
			console.log(e)
			this.repairItemPhoto = e[0]
		},
		rateChange(e) {
			let score = e.value
			this.star = score
			this.scoreType = this.scoreTypeList[score - 1]
		},
		commentHandle(e) {
			this.comment = e.detail.value
			console.log('comment :  ---- ' + this.comment)
		},
		commitRate() {
			let that = this
			let orderId = that.orderId
			that.$login.checkTokenValidity().then((login) => {
				that.$api.apiPost({
					url: `/api/order/${orderId}/evaluation`,
					data: {
						star: that.star,
						comment: that.comment,
						shopId: that.shopId,
						repairItemPhoto: that.repairItemPhoto
					}
				})
				.then((res) => {
					uni.showToast({
						title: '感谢你的评价！',
						icon: 'none',
						
					})
					that.$websocket.sendData({
						orderId: that.orderId,
						orderStep: 12,
						notifyShopId:that.shopId,
						// notifyCustomerId: order.customerId
					})
					uni.navigateBack()
				})
				.catch((res) => {
					uni.showToast({
						title: '提交评价失败！',
						icon: 'none'
					})
				})
			})
		}
	}
}
